<template>
    <div id="app-main">
        <router-view></router-view>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                location: null
            }
        },
        created() {
            this.location = location.pathname;
        },
        computed: {
            isNotIndex() {
                return this.$route.path !== "/";
            }
        },
        methods: {
            toUrl() {
                this.$router.push(this.location);
            },
            refresh() {
                location.reload();
            },
            back() {
                this.$router.back();
            }
        }
    }
</script>
